@font-face {
  font-family: "Raleway";
  src: url("../fonts/Raleway-Regular.woff2") format("woff2"), url("../fonts/Raleway-Regular.woff") format("woff");
  font-style: normal;
  font-weight: 400;
  font-display: swap;
}
@font-face {
  font-family: "Comfortaa";
  src: url("../fonts/Comfortaa-Regular.woff2") format("woff2"), url("../fonts/Comfortaa-Regular.woff") format("woff");
  font-style: normal;
  font-weight: 400;
  font-display: swap;
}
.container {
  padding: 0 26px;
}
@media (min-width: 1140px) {
  .container {
    width: 1128px;
    margin: 0 auto;
  }
}
.body {
  margin: 0;
  display: flex;
  flex-direction: column;
  min-height: 100%;
}
.title {
  font-family: "Comfortaa", "Arial", sans-serif;
  font-size: 36px;
  margin: 0;
  color: #0f805d;
  font-weight: 400;
  text-align: center;
}
.logo {
  display: block;
}
.logo svg {
  fill: none;
}
.logo__text {
  display: none;
}
.button {
  font-family: "Raleway", "Arial", sans-serif;
  background: none;
  border: none;
  padding: 16px 0 16px 0;
  cursor: pointer;
  background-color: #6a107a;
  color: #fbfcfe;
  font-size: 20px;
  width: 100%;
  border-radius: 40px;
  letter-spacing: 1px;
}
.main-list {
  list-style-type: none;
  padding: 0;
  margin: 0;
  font-family: "Raleway";
  display: none;
}
.main-list__link {
  display: block;
  font-size: 20px;
  text-decoration: none;
  color: #000000;
  padding: 16px 0 16px 40px;
}
.main-header {
  background-color: #f1f6fc;
  padding-top: 8px;
  position: relative;
  box-shadow: 0 4px 4px rgba(146, 81, 155, 0.37);
}
.main-header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.benefits {
  list-style-type: none;
  padding: 0;
  margin: 0;
  font-family: "Raleway", "Arial", sans-serif;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(210px, 1fr));
  gap: 12px;
}
.benefits__item {
  display: flex;
  align-items: center;
  column-gap: 12px;
}
.benefits__item--leaf::before {
  content: url("../img/icons/leaf.svg");
  position: relative;
  width: 32px;
  height: 32px;
}
.benefits__item--snowflake::before {
  content: url("../img/icons/snowflake.svg");
  position: relative;
  width: 32px;
  height: 32px;
}
.benefits__item--delivery::before {
  content: url("../img/icons/delivery.svg");
  position: relative;
  width: 32px;
  height: 32px;
}
.banner {
  background: url("../img/background-image-mobile.jpg") no-repeat;
  background-position: center 75%;
  background-size: cover;
  min-height: 640px;
  padding-top: 68px;
  box-sizing: border-box;
}
.banner__benefits {
  width: calc(100% - 58px);
  margin: 0 auto;
}
.banner__title {
  margin-bottom: 32px;
}
.banner__button {
  margin-top: 48px;
}
.navigation__main-list {
  position: absolute;
  left: 0;
  z-index: 2;
  top: 67px;
  background-color: #f1f6fc;
  width: 100%;
  box-sizing: border-box;
  padding: 16px 0 136px;
}
.navigation__button {
  padding: 0;
  background: none;
}
.catalog {
  padding: 64px 0;
}
.catalog__title {
  margin-bottom: 64px;
}
.catalog__content {
  list-style-type: none;
  padding: 0;
  margin: 0;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(264px, 1fr));
  gap: 24px;
}
@media (min-width: 768px) {
  .catalog__content {
    gap: 52px;
  }
}
.catalog__item {
  width: calc(100% - 4px);
}
.product {
  background-color: #fbfcfe;
  border-radius: 40px;
  filter: drop-shadow(0px 4px 4px rgba(88, 13, 101, 0.15));
}
.product__block {
  padding: 28px 20px 54px 24px;
}
.product__link {
  display: block;
  text-decoration: none;
  width: 184px;
  text-align: center;
  margin: 0 auto;
  margin-bottom: 28px;
}
.product__title {
  font-size: 28px;
  color: #0f805d;
  font-family: "Raleway", "Arial", sans-serif;
  font-weight: 400;
  margin: 0;
}
.product__image {
  display: block;
  width: 100%;
  height: auto;
  object-fit: cover;
  border-radius: 40px 40px 0px 0px;
}
.product__characteristics {
  margin: 0;
  font-family: "Raleway", "Arial", sans-serif;
  font-size: 18px;
  font-style: normal;
  display: flex;
  column-gap: 12px;
  justify-content: center;
  margin-bottom: 38px;
}
.product__weight {
  margin: 0;
}
.news {
  padding-top: 96px;
  padding-bottom: 64px;
  position: relative;
}
.news::before {
  content: url(../img/icons/line-leaf.svg);
  position: absolute;
  width: 228px;
  height: 34px;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
}
.news__title {
  margin-bottom: 64px;
}
.news__content {
  list-style-type: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  gap: 24px;
  justify-content: center;
}
.news__card {
  width: 268px;
}
@media (min-width: 768px) {
  .news__card {
    width: 348px;
  }
}
.news-card {
  font-family: "Raleway", "Arial", sans-serif;
  border: 2px solid #0f805d;
  border-radius: 40px;
  box-sizing: border-box;
  padding: 32px 20px 72px 20px;
  min-height: 400px;
  display: flex;
  flex-direction: column;
  position: relative;
}
.news-card::after {
  content: url("../img/icons/one-leaf.svg");
  position: absolute;
  width: 76px;
  height: 32px;
  bottom: 24px;
  left: 50%;
  transform: translateX(-50%);
}
.news-card__title {
  font-size: 20px;
  font-weight: 400;
  margin: 0;
  margin-bottom: 37px;
  text-align: center;
}
.news-card__review {
  font-size: 14px;
  line-height: 17px;
  margin: 0;
}
.news-card__data {
  font-size: 14px;
  margin-top: auto;
  text-align: end;
}
.contacts {
  position: relative;
  padding-top: 96px;
  padding-bottom: 64px;
}
.contacts::before {
  content: url(../img/icons/line-leaf.svg);
  position: absolute;
  width: 228px;
  height: 34px;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
}
.contacts__title {
  margin-bottom: 64px;
}
.contacts__text {
  font-family: "Raleway", "Arial", sans-serif;
  margin: 0;
  font-size: 17px;
  text-align: center;
  margin-bottom: 34px;
}
.contacts__list {
  list-style-type: none;
  padding: 0;
  margin: 0;
  font-family: "Raleway", "Arial", sans-serif;
  font-size: 15px;
  display: flex;
  flex-wrap: wrap;
  row-gap: 18px;
  column-gap: 24px;
  justify-content: space-between;
  margin-bottom: 34px;
}
.contacts__link {
  display: block;
  text-decoration: none;
  color: #000000;
  display: flex;
  column-gap: 13px;
  align-items: center;
}
.contacts__link address {
  font-style: normal;
}
.contacts__link span {
  display: block;
}
.contacts__link:hover span {
  color: #7bdb0a;
}
.contacts__link:hover svg {
  stroke: #7bdb0a;
}
.contacts__link:active span {
  color: #0f805d;
}
.contacts__link:active svg {
  stroke: #0f805d;
}
.contacts__map-image {
  width: 100%;
  height: 372px;
  object-fit: cover;
  border-radius: 40px;
}
.social-list {
  list-style-type: none;
  padding: 0;
  margin: 0;
  display: grid;
  grid-template-columns: repeat(auto-fit, 32px);
  max-width: 136px;
  gap: 20px;
  margin-left: auto;
}
.social-list__link svg {
  fill: none;
}
.main-footer {
  background-color: #f1f6fc;
  padding-top: 11px;
  padding-bottom: 20px;
}
.main-footer__container {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}
@media (max-width: 427px) {
  .main-footer__container {
    column-gap: 40px;
  }
}
@media (min-width: 427px) {
  .main-footer__container {
    display: grid;
    grid-template-columns: 52px 136px 110px;
    place-content: center;
    justify-content: space-between;
  }
}
.main-footer__text-link {
  font-family: "Raleway", "Arial", sans-serif;
  display: flex;
  column-gap: 8px;
  align-items: center;
  margin-left: auto;
  font-size: 11px;
  text-decoration: none;
  color: #000000;
  margin-top: 7px;
}
@media (min-width: 427px) {
  .main-footer__text-link {
    margin-top: 0;
  }
}
